@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

* > * {
    font-family: "Poppins", cursive;
    margin: 0;
}

body{
  background: #fdfdfd;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #fdfdff, #fcfcfd, #fcfcfd);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #fafafc, #fdfdfd, #fcfcfd); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
}

.App {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.register{

  width: 100vw;
  display:flex;
  border: 1px solid #e9ecef;
  border-radius: 5px;
  /* box-shadow: 1px 3px 10px #e9ecef; */
  background-color: white;
  padding-top: 15%;
}

.register span{
  font-size: 25px;
  color: #4a137e;
  
}

#form{
  max-width: 320px;
  width: 100vw;
  margin: 2em auto;
}

#form >  input, .btn{
  border: 1px solid #e9ecef;
  padding: .7em 1em;
}

#form > input:focus{
  outline: none;
}

#form > .btn{
  background-color: #371b6d;
  color: #e9ecef;
  font-size: 1em;
  cursor: pointer;
}

.flex{
  display: flex;
  gap: 1em;
}

.flex-col{
  flex-direction: column;
}

.register .col-1{
  margin: auto;
  padding: 3em ;
  right: 0px;
}
.tech{
  font-family: 'Quantico';
font-size: 53px;
text-align: center;
color: #fffefe;
flex: none;
order: 2;
flex-grow: 0;

}
.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media (max-width: 767px) {
  .hidden-mobile {
    display: none;
  }
}
.centered1 {
  position: absolute;
  top: 45%;
  left: 50%;
  flex: none;
order: 2;
flex-grow: 0;
font-size: 15px;
  transform: translate(-50%, -50%);
}
.centered2 {
  position: absolute;
  top: 55%;
  flex: none;
order: 2;
flex-grow: 0;
font-size: 12px;
  left: 50%;
  transform: translate(-50%, -50%);
}

.container {
  position: relative;
  text-align: center;
  color: white;
}

.register .col-2 img{
  width: 50vw;
  height: 100%;
  object-fit: cover;
  align-self: flex-end;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  display: none;
  background-image: linear-gradient(152.17deg, #6750A4 0.89%, #65E7E7 87.89%);
}
@media screen and (min-width: 860px)  {
  .register .col-2 img{
    display: initial;
  }
}
.frame{
  display: flex;
padding: 0px;
flex: none;
order: 0;
flex-grow: 0;
}
.frame1{
height: 20px;
width: 20px;
background: white;
position: absolute;  
}
p{
  text-align: left;
  font-size: 15px;
}